export default defineComponent({
  name: "App",
  components: {
    JsonForms,
    JsonViewer
  },
  data() {
    return {
      // freeze renderers for performance gains
      renderers: Object.freeze(renderers),
      data,
      schema,
      uischema
    };
  },
  methods: {
    onChange(event: JsonFormsChangeEvent) {
      this.data = event.data;
    },
    getData () {
      return this.data
    }
  },
  provide() {
    return {
      styles: myStyles,
    };
  },
});
